@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap);
body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
}

.header {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  height: 60px;
  width: 100%;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.05);
}

.navigation_list {
  display: flex;
  width: 200px;
  margin: auto;
  justify-content: space-between;
  margin-right: 100px;
}

.navigation_list li {
  list-style-type: none;
}

.navigation_list a {
  text-transform: uppercase;
  text-decoration: none;
  color: #000;
  font-size: 12px;
  font-weight: 500;
}

.navigation_list a:hover {
  color: #00ACDC;
}

.activeLink {
  color: #00ACDC;
}
.product {
  width: 200px;
  padding: 10px;
}

.product_title {
  text-align: center;
}

.product_description {
  font-size: 13px;
}

.product_price {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.product_price > span {
  font-size: 22px;
  font-weight: bold;
}

.product_buttonBuy {
  height: 40px;
  outline: none;
  width: 100%;
  border: none;
  background-color: #00ACDC;
  border-radius: 5px;
  cursor: pointer;
  color: #fff;
  text-transform: uppercase;
  font-size: 16px;
  font-weight: bold;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
}

.product_buttonBuy:hover {
  color: #00ACDC;
  background: #fff;
  border: 1px solid #00ACDC;
}
.catalog {
  display: flex;
  justify-content: space-between;
  padding: 50px 20px 0;
}
